.ZoomImage {
    cursor: zoom-in;
    display: block;
    // max-width: 200px;
    // max-height: 200px;
    // max-width: 100%;
    margin: auto;
  }
  img.zoomed {
    cursor: zoom-out;
  }
  .frame {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: none;
    background: white;
    transform: translateZ(0);
  }